import React, { Component } from 'react';
import { List, Avatar, Divider, Modal, Button } from 'antd';
import { UserOutlined, PushpinFilled, LikeFilled } from '@ant-design/icons';
import './MyInfo.css';
import ModalPage from '../Main/ModalPage';

class MyInfo extends Component {
   constructor(props) {
      super(props);
      this.state = {
         modalVisible: false,
         currentMovie: {},
      };
   }

   setModalVisible = (modalVisible) => {
      this.setState({ modalVisible: modalVisible });
   };

   handleCurrentSavedMovie = (currentMovieId) => {
      const { savedMovie } = this.props.profile;

      const currentMovie = savedMovie.filter((movie) => {
         return movie.id === currentMovieId;
      });

      this.setState({
         modalVisible: true,
         currentMovie: currentMovie[0],
      });
   };

   handleCurrentLikedMovie = (currentMovieId) => {
      const { likedMovie } = this.props.profile;

      const currentMovie = likedMovie.filter((movie) => {
         return movie.id === currentMovieId;
      });

      this.setState({
         modalVisible: true,
         currentMovie: currentMovie[0],
      });
   };

   render() {
      const {
         loginID,
         username,
         profileImg,
         provider,
         savedMovie,
         likedMovie,
      } = this.props.profile;

      const { modalVisible, currentMovie } = this.state;

      return (
         <div>
            <List
               itemLayout="vertical"
               dataSource={[
                  {
                     loginID: loginID,
                     username: username,
                     provider: provider,
                     profileImg: profileImg,
                  },
               ]}
               renderItem={(item) => (
                  <List.Item>
                     <List.Item.Meta
                        avatar={
                           item.profileImg ? (
                              <Avatar src={profileImg} size={92} />
                           ) : (
                              <Avatar size={92} icon={<UserOutlined />} />
                           )
                        }
                        title={
                           <strong className="myInfo-username">
                              {item.username}님
                           </strong>
                        }
                        description={
                           <strong>아이디 또는 이메일: {item.loginID}</strong>
                        }
                     />
                  </List.Item>
               )}
            />

            <Divider orientation="left" plain>
               <strong className="divider-text">
                  {<PushpinFilled />} 저장한 영화 리스트
               </strong>
            </Divider>
            <List
               itemLayout="horizontal"
               dataSource={savedMovie}
               renderItem={(item) => (
                  <List.Item>
                     <List.Item.Meta
                        avatar={
                           <Avatar
                              src={JSON.parse(item.posters)[0]}
                              size="large"
                              className="myInfo-savedMovie-img"
                              onClick={() =>
                                 this.handleCurrentSavedMovie(item.id)
                              }
                           />
                        }
                        title={
                           <strong
                              className="myInfo-savedMovie-title"
                              onClick={() =>
                                 this.handleCurrentSavedMovie(item.id)
                              }
                           >
                              {(item.title + item.titleEng).length > 20
                                 ? (
                                      item.title +
                                      '(' +
                                      item.titleEng +
                                      ')'
                                   ).substring(0, 16) + '...'
                                 : item.title + '(' + item.titleEng + ')'}
                           </strong>
                        }
                     />
                     <div>⭐ {item.userRating}</div>
                  </List.Item>
               )}
            />
            <Divider orientation="left" plain>
               <strong className="divider-text">
                  {<LikeFilled />} 좋아요를 누른 영화 리스트
               </strong>
            </Divider>
            <List
               itemLayout="horizontal"
               dataSource={likedMovie}
               renderItem={(item) => (
                  <List.Item>
                     <List.Item.Meta
                        avatar={
                           <Avatar
                              src={JSON.parse(item.posters)[0]}
                              size="large"
                              className="myInfo-likedMovie-img"
                              onClick={() =>
                                 this.handleCurrentLikedMovie(item.id)
                              }
                           />
                        }
                        title={
                           <strong
                              className="myInfo-likedMovie-title"
                              onClick={() =>
                                 this.handleCurrentLikedMovie(item.id)
                              }
                           >
                              {(item.title + item.titleEng).length > 20
                                 ? (
                                      item.title +
                                      '(' +
                                      item.titleEng +
                                      ')'
                                   ).substring(0, 16) + '...'
                                 : item.title + '(' + item.titleEng + ')'}
                           </strong>
                        }
                     />
                     <div>⭐ {item.userRating}</div>
                  </List.Item>
               )}
            />

            <Modal
               title="SF CINEMA"
               centered
               width={1150}
               visible={modalVisible}
               onOk={() => this.setModalVisible(false)}
               onCancel={() => this.setModalVisible(false)}
               footer={null}
            >
               <ModalPage
                  currentMovie={currentMovie}
                  isLogin={this.props.isLogin}
               />
            </Modal>
         </div>
      );
   }
}

// eslint-disable-next-line
export default MyInfo;
// eslint-disable-next-line
